#bioSection {
    display: flex;
    flex-direction: column;
}

#bioText {
    order: 0;
    text-align: left;
    text-align: initial;
    font-size: 11vmin;
    padding: 0 12vmin;
}

#autor {
    order: 1;
    font-size: 4vmin;
    padding: 0 12vmin;
    text-align: right;
}
.albunsCapas {
    background-color: #bba34d;
    display: flex;
    align-items: center;
    justify-content: center;
}

.fp-section {
    text-align: center
}

.capas {
    width: 60vmin;
    height: 60vmin;
}

.fab {
    font-size: calc(15px + 6vmin);
    margin: 5vmin 5vmin 0 5vmin
}

.fab.fa-spotify:hover {
    color: #1e7d15
}

.fab.fa-youtube:hover {
    color: #dc1313
}

